import * as React from 'react';
import { graphql } from 'gatsby';
import { MDXProvider } from '@mdx-js/react';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import Layout from '~components/Layout/Template/Layout';
import MdxLink from '../i18n/components/mdxLink';
import ImgSharp from '~components/utils/ImgSharp/ImgSharp';
import AuthorList from '~components/Sections/AuthorList/AuthorList';
import useIntlHelper from '~components/utils/Intl/IntlHelper';
import Link from '~components/utils/Link/Link';
import Subscribe from '~components/Layout/Subscribe/SubscribeForm/SubscribeForm';
import ReadNext from '~components/Sections/ReadNext/ReadNext';

const PostHero = ({ post, stickyRef }) => {
	const { formatMessage, intl } = useIntlHelper();
	return (
		<header className="post-full-header">
			<div className="inner">
				{post.frontmatter.tags && (
					<section className="post-full-tags">
						<Link to={`/tags/${post.frontmatter.tags[0]}/`}>
							{formatMessage('tags.' + post.frontmatter.tags[0] + '.title')}
						</Link>
					</section>
				)}

				<h1 ref={stickyRef} className="post-full-title">
					{post.frontmatter.title}
				</h1>

				{post.excerpt && (
					<p className="post-full-custom-excerpt">{post.excerpt}</p>
				)}

				<div className="post-full-byline">
					<section className="post-full-byline-content">
						<AuthorList authors={post.frontmatter.authors} isPost={true} />

						<section className="post-full-byline-meta">
							<h4 className="author-name">
								{post.frontmatter.authors.map((author, i) => (
									<span key={i}>
										{/* 		<Link key={i} to={`/authors/${author.id}/`}> */}
										{intl.formatMessage({
											id: `authors.${author.id}.title`,
										})}
										{/* 	</Link>*/}
									</span>
								))}
							</h4>
							<div className="byline-meta-content">
								<time
									className="byline-meta-date"
									dateTime={post.frontmatter.date}
								>
									{intl.formatDate(post.frontmatter.date, {
										year: 'numeric',
										month: 'short',
										day: 'numeric',
									})}
								</time>
								<span className="byline-reading-time">
									<span className="bull">&bull;</span>
									{post.timeToRead + ' '} {formatMessage('post.timeToRead')}
								</span>
							</div>
						</section>
					</section>
				</div>
			</div>
		</header>
	);
};
const PostTemplate = ({ data }) => {
	const { post } = data;

	const stickyRef: React.RefObject<HTMLDivElement> = React.useRef(null);

	return (
		<>
			<Layout template="post" stickyRef={stickyRef} data={data}>
				<div className="inner">
					<article className="post-full post no-image page no-image">
						<PostHero post={post} stickyRef={stickyRef} />
						<figure className="post-full-image">
							<ImgSharp
								className="kg-card kg-code-card"
								fluid={post.frontmatter.image.childImageSharp.fluid}
								alt={post.frontmatter.title}
							/>
						</figure>

						<section className="post-full-content">
							<div className="post-content">
								<MDXProvider
									components={{
										a: MdxLink,
									}}
								>
									<MDXRenderer>{post.body}</MDXRenderer>
								</MDXProvider>
							</div>
						</section>
					</article>
					<Subscribe />

					{/* 					<ReadNext
						relatedPosts={data.relatedPosts}
						primaryTag={data.post.frontmatter.tags[0]}
					/> */}
				</div>
			</Layout>
		</>
	);
};

export default PostTemplate;

export const PostTemplateQuery = graphql`
	query Post($title: String!, $locale: String!, $primaryTag: String!) {
		post: mdx(
			frontmatter: { title: { eq: $title } }
			fields: { locale: { eq: $locale } }
		) {
			frontmatter {
				date(formatString: "DD MMMM YYYY")
				title
				tags
				image {
					childImageSharp {
						fluid {
							...GatsbyImageSharpFluid_withWebp_tracedSVG
						}
					}
				}
				authors {
					id
					bio
					avatar {
						childImageSharp {
							fluid {
								...GatsbyImageSharpFluid_withWebp_tracedSVG
							}
						}
					}
				}
			}
			excerpt(truncate: true, pruneLength: 200)
			timeToRead
			body
		}
		relatedPosts: allMdx(
			filter: {
				frontmatter: { tags: { in: [$primaryTag] }, title: { ne: $title } }
				fields: { locale: { eq: $locale } }
			}
			limit: 5
		) {
			nodes {
				id
				frontmatter {
					title
					tags
					authors {
						id
					}
					image {
						childImageSharp {
							fluid {
								...GatsbyImageSharpFluid_withWebp_tracedSVG
							}
						}
					}
					date(formatString: "DD MMMM YYYY")
				}
				timeToRead
				fields {
					slug
				}
			}
			totalCount
		}
	}
`;
